import React from 'react'
import { Modal, Descriptions, Input, Select, Button, message } from 'antd'
import {postAction} from '@/axios'
const { Option } = Select;

class EditModal extends React.Component {
    state = {
        dataSourceId: '',//当前数据源id
        dataSourceName: '', // 数据源名称
        dataSourceType:'',
        host: '', // 服务器IP
        port: '', // 服务器端口
        databaseName: '', //数据库名
        serviceType: '1', // oracle服务类型
        serviceName: '', //服务名/SID
        userName: '', //用户名
        password: '', // 密码
        schemaName: '', //模式
        url: '',// URL
        sureBool: true,
        sureType: "default"
    }

    // 数据源名称
    dataSourceNameChange = (e) => {
        this.setState({
            dataSourceName: e.target.value
        })
        this.safeChange()
    }

    // 服务器IP
    severIpChange = (e) => {
        this.setState({
            host: e.target.value
        })
        this.safeChange()
    }

    // 服务器端口
    severPortChange = (e) => {
        this.setState({
            port: e.target.value
        })
        this.safeChange()
    }

    // 数据库名
    severNameChange = (e) => {
        this.setState({
            databaseName: e.target.value
        })
        this.safeChange()
    }

    // oracle服务类型
    oracleSeverTypeChange = (value) => {
        this.setState({
            serviceType: value
        })
        this.safeChange()
    }

    // 服务名/SID
    oracleSeverChange = (e) => {
        this.setState({
            serviceName: e.target.value
        })
        this.safeChange()
    }

    // 用户名
    userChange = (e) => {
        this.setState({
            userName: e.target.value
        })
        this.safeChange()
    }

    // 密码
    passwordChange = (e) => {
        this.setState({
            password: e.target.value
        })
        this.safeChange()
    }

    // 模式
    modeChange = (e) => {
        this.setState({
            schemaName: e.target.value
        })
        this.safeChange()
    }

    // url
    urlChange = (e) => {
        this.setState({
            url: e.target.value
        })
        this.safeChange()
    }

    // 任何输入变化需要重新测试
    safeChange = () => {
        this.setState({
            sureBool: true,
            sureType: "default"
        })
    }

    // 测试
    test = () => {
        let bool = this.dataCheck();
        if (bool) {
            let param = {
                "dataSourceName": this.state.dataSourceName,
                "dataSourceType": this.state.dataSourceType,
                "url": this.state.url,
                "host": this.state.host,
                "port": this.state.port,
                "serviceType": this.state.serviceType,
                "serviceName": this.state.serviceName,
                "databaseName": this.state.databaseName,
                "schemaName": this.state.schemaName,
                "userName": this.state.userName,
                "password": this.state.password
            }
            postAction(
                `/ybt/data/datasource/test`,
                param
            ).then(res => {
                if (res.success) {
                    message.success("链接成功")
                    this.setState({
                        sureBool: false,
                        sureType: "primary"                        
                    })
                } else {
                    message.error('链接失败')
                }
            })
        }
    }

    // 链接成功后添加数据源
    sureClick = () => {
        let bool = this.dataCheck();
        if (bool) {
            // 更新数据源列表
            // 更新数据源列表
            let param = {
                "dataSourceId": this.state.dataSourceId,
                "dataSourceName": this.state.dataSourceName,
                "dataSourceType": this.state.dataSourceType,
                "url": this.state.url,
                "host": this.state.host,
                "port": this.state.port,
                "serviceType": this.state.serviceType,
                "serviceName": this.state.serviceName,
                "databaseName": this.state.databaseName,
                "schemaName": this.state.schemaName,
                "userName": this.state.userName,
                "password": this.state.password
            }
            postAction(
                `/ybt/data/datasource/save`,
                param
            ).then(res => {
                if (res.success) {
                    message.success("操作成功")
                    this.props.hideEditModal()
                } else {
                    message.error(res.obj)
                }
            })
        }
    }

    // 必填项测试
    dataCheck = () =>{
        let {dataSourceName, host, port, databaseName, serviceName, userName, password,dataSourceType} = this.state;
        if (!dataSourceName) {
            message.error("请输入数据源名称", 5)
            return false
        }

        if (!host) {
            message.error("请输入服务器IP", 5)
            return false
        }

        if (!port) {
            message.error("请输入服务器端口", 5)
            return false
        }

        if (dataSourceType !== 'oracle') {
            if (!databaseName) {
                message.error("请输入数据库名", 5)
                return false
            }
        }

        if (dataSourceType === 'oracle' && !serviceName) {
            message.error("请输入服务名/SID", 5)
            return false
        }

        if (!userName) {
            message.error("请输入用户名", 5)
            return false
        }

        if (!password) {
            message.error("请输入密码", 5)
            return false
        }

        return true

    }

    componentDidMount() {        
        let {dataSourceType,dataSourceId,dataSourceName, host, port, databaseName,serviceType, serviceName, userName, password,schemaName,url} = this.props.currentDataSource
        this.setState({dataSourceType,dataSourceId,dataSourceName, host, port, databaseName,serviceType, serviceName, userName, password,schemaName,url})
    }

    render() {
        return (
            <Modal
                title={`${this.state.dataSourceType}编辑数据源`}
                visible={true}
                onCancel={this.props.hideEditModal}
                footer={null}
                width="900px"
                centered
            >
                {
                    this.state.dataSourceType === "oracle"
                        ?
                        <Descriptions column={1} bordered={true} size='small'>
                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>数据源名称</span>)}>
                                <Input value={this.state.dataSourceName} onChange={this.dataSourceNameChange} placeholder='请输入数据源名称' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>服务器IP</span>)}>
                                <Input value={this.state.host} onChange={this.severIpChange} placeholder='请输入服务器IP' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>服务器端口</span>)}>
                                <Input value={this.state.port} onChange={this.severPortChange} placeholder='请输入服务器端口' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "white", marginRight: "5px" }}>*</span>类型</span>)}>
                                <Select value={this.state.serviceType} style={{ width: "100%" }} onChange={this.oracleSeverTypeChange}>
                                    <Option value="1">服务名</Option>
                                    <Option value="2">SID</Option>
                                </Select>
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>服务名/SID</span>)}>
                                <Input value={this.state.serviceName} onChange={this.oracleSeverChange} placeholder='请输入服务名/SID' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>用户名</span>)}>
                                <Input value={this.state.userName} onChange={this.userChange} placeholder='请输入用户名' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>密码</span>)}>
                                <Input value={this.state.password} onChange={this.passwordChange} placeholder='请输入密码' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "white", marginRight: "5px" }}>*</span>模式</span>)}>
                                <Input value={this.state.schemaName} onChange={this.modeChange} placeholder='请输入模式' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "white", marginRight: "5px" }}>*</span>URL</span>)}>
                                <Input value={this.state.url} onChange={this.urlChange} placeholder='请输入URL' />
                            </Descriptions.Item>
                        </Descriptions>
                        :
                        <Descriptions column={1} bordered={true} size='small'>
                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>数据源名称</span>)}>
                                <Input value={this.state.dataSourceName} onChange={this.dataSourceNameChange} placeholder='请输入数据源名称' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>服务器IP</span>)}>
                                <Input value={this.state.host} onChange={this.severIpChange} placeholder='请输入服务器IP' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>服务器端口</span>)}>
                                <Input value={this.state.port} onChange={this.severPortChange} placeholder='请输入服务器端口' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>数据库名</span>)}>
                                <Input value={this.state.databaseName} onChange={this.severNameChange} placeholder='请输入数据库名' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>用户名</span>)}>
                                <Input value={this.state.userName} onChange={this.userChange} placeholder='请输入用户名' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "red", marginRight: "5px" }}>*</span>密码</span>)}>
                                <Input value={this.state.password} onChange={this.passwordChange} placeholder='请输入密码' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "white", marginRight: "5px" }}>*</span>模式</span>)}>
                                <Input value={this.state.schemaName} onChange={this.modeChange} placeholder='请输入模式' />
                            </Descriptions.Item>

                            <Descriptions.Item label={(<span><span style={{ color: "white", marginRight: "5px" }}>*</span>URL</span>)}>
                                <Input value={this.state.url} onChange={this.urlChange} placeholder='请输入URL' />
                            </Descriptions.Item>
                        </Descriptions>
                }

                <p style={{ textAlign: "center", marginTop: "30px" }}>
                    <Button type='primary' onClick={this.test}>测试</Button>
                    <Button type={this.state.sureType} style={{ margin: "0px 15px" }} disabled={this.state.sureBool} onClick={this.sureClick}>确定</Button>
                    <Button type='default' onClick={() => this.props.hideEditModal()}>取消</Button>
                </p>
            </Modal>
        )
    }
}

export default EditModal