import React from 'react';
import { Tabs } from 'antd';
import { withRouter } from 'react-router-dom';
import styles from './index.module.less';

/**
 * 
 * @param { props } activeTab string;
 */

function DataTabs(props){
    const menuList = JSON.parse(window.localStorage.getItem("leftMenuList")).menus;
    const dataMenu = menuList.find(item => item.url==="/dataManagement") || {};
    const dataTabs = dataMenu.subMenus || [];
    const callback = (key) => {
        props.history.push(`/dataManagement/${key}`)
    }
    return (
        <Tabs activeKey={props.activeTab} onChange={callback} className={styles.tabs}>
            {
                dataTabs.map(item => (<Tabs.TabPane tab={item.name} key={item.url.replace("/dataManagement/","")}></Tabs.TabPane>))
            }
            {/* <Tabs.TabPane tab="数据源管理" key="dataSourceManagement"></Tabs.TabPane>
            <Tabs.TabPane tab="数据集管理" key="dataSetManagement"> </Tabs.TabPane> */}
        </Tabs>
    )
}

export default withRouter(DataTabs)