import React from 'react'
import {Button, Input, List, Avatar, Icon,message,Modal} from 'antd'
import MySQL from './MySQL.png'
import Oracle from './Oracle.png'
import SQLServer from './SQLServer.png'
import AddDataSource from './AddDataSource'
import EditModal from './editModal'
import './leftPart.css'
import {getAction} from '@/axios'
import {hasButtonPermission} from '@/util'
const { Search } = Input;

class LeftPart extends React.Component{
    state = {
        list: [],
        addModal: false, // 新增数据源模块
        currentDataSource: null, // 当前选中的数据源信息
        editModal: false,
        searchWord:''
    }

    // 点击数据源列表
    handleClick = (item) => {
        this.setState({
            currentDataSource: item
        }, () => {
            this.props.changeCurrentDataSource(this.state.currentDataSource)
        })
    }

    // 编辑数据源窗口
    handleEdit = (item) => {
        this.setState({
            currentDataSource: item,
            editModal: true
        })
    }

    // 关闭编辑数据源窗口
    hideEditModal = () => {
        this.setState({
            editModal: false
        },()=>{
            this.getData()
        })
    }

    // 删除
    handleDelete = (item) => {
        // 1，调用接口，删除当前数据源

        // 2，调用接口，重新请求列表数据，

        // 3，重置当前选中的数据源数据
        Modal.confirm({
            title:'删除数据源',
            content:'确定删除该数据源吗？',
            okText:'确定',
            cancelText:'取消',
            onOk:()=>{
                getAction(
                    `/ybt/data/datasource/delete`,
                    {
                        dataSourceId:item.dataSourceId
                    }
                ).then(res=>{
                    if (res.success) {
                        message.success("操作成功")
                        this.setState({
                            currentDataSource:null
                        },()=>{
                            this.props.clearDataSource()
                            this.getData()
                        })
                    } else {
                        message.error(res.obj)
                    }
                })
            }            
        })
        
    }

    // 新增数据源
    addHandle = () => {
        this.setState({
            addModal: true
        })
    }

    // 关闭新增数据源弹窗
    hideAddModal = () => {
        this.setState({
            addModal: false
        },()=>{
            this.getData()
        })
    }
    componentDidMount() {
        this.getData()
    }
    handleSearch=(value)=>{
        this.setState({
            searchWord:value
        },()=>{
            this.getData()
        })
    }
    addSuccess=()=>{
        // this.getData()
    }
    getData=()=>{
        getAction(
            `/ybt/data/datasource/list`,
            {
                dataSourceName:this.state.searchWord
            }
        ).then(res=>{
            if(res.success){
                this.setState({
                    list:res.obj
                })
            }
        })
    }
    render () {
        return (
            <div className="data-source-leftpart">
                <div style={{padding:"15px 20px", lineHeight: "30px"}}>
                    <span style={{fontSize:"16px", fontWeight:"bold", color: "rgba(0, 0, 0, 0.85)"}}>我的数据源</span>
                    {
                        hasButtonPermission('dataSource001') && 
                        <Button type="primary" icon="plus" style={{float: "right"}} onClick={this.addHandle}>新增数据源</Button>

                    }
                    <Search
                        placeholder="请输入关键字搜索"
                        onSearch={this.handleSearch}
                        style={{ margin: "10px 0px"}}
                    />
                </div>

                <List
                    itemLayout="horizontal"
                    dataSource={this.state.list}
                    renderItem={
                        (item) => {
                            let pic = null;
                            if (item.dataSourceType === 'mysql') {
                                pic = MySQL
                            } else if (item.dataSourceType === 'SQL Server') {
                                pic = SQLServer
                            } else if (item.dataSourceType === 'oracle') {
                                pic = Oracle
                            }
                            return (
                                <List.Item 
                                onClick={this.handleClick.bind(this, item)} 
                                style={{padding: "10px 30px", border: "none"}} 
                                className={this.props.currentDataSource && item.dataSourceId === this.props.currentDataSource.dataSourceId ? 'active':''}
                                actions={
                                    [
                                        hasButtonPermission('dataSource002')?
                                        <Icon 
                                            onClick={this.handleEdit.bind(this, item)} 
                                            title='编辑' type="edit" 
                                            style={{fontSize:"18px"}} />:null,
                                        hasButtonPermission('dataSource003')?
                                        <Icon 
                                            onClick={this.handleDelete.bind(this, item)} 
                                            title='删除' 
                                            type="delete" 
                                            style={{fontSize:"18px"}} />:null
                                    ]
                                }>
                                    <List.Item.Meta
                                        avatar={
                                            <Avatar src={pic} shape='square' size={68} />
                                        }
                                        title={<span style={{lineHeight: "34px"}}>{item.dataSourceName}</span>}
                                        description={`所有者：${item.creatorName}`}
                                    />
                                </List.Item>
                            )
                        }
                    }
                />

                {
                    this.state.addModal
                    ?
                    <AddDataSource addSuccess={this.addSuccess} hideAddModal = {this.hideAddModal} />
                    :
                    null
                }

                {
                    this.state.editModal
                    ?
                    <EditModal currentDataSource = {this.state.currentDataSource} hideEditModal = {this.hideEditModal} />
                    :
                    null
                }
            </div>
        )
    }
}

export default LeftPart