import React from 'react'
import { Descriptions } from 'antd';

class RightPart extends React.Component{

    state = {
        dataSourceName : '随便什么名字吧', // 数据源名称
        severIp: '是随便什么IP吧', // 服务器IP
        severPort: '随便什么', // 服务器端口
        severName: '随便什么', //数据库名
        oracleSeverType: '0', // oracle服务类型
        oracleSever: 'afafsadsadsadsa', //服务名/SID
        user: '随便什么', //用户名
        password : '随便什么', // 密码
        mode: '随便什么', //模式
        url: '随便什么',// URL
    }

    render () {
        if (this.props.currentDataSource) {

            if (this.props.currentDataSource.dataSourceType === 'oracle') {
                return (
                    <div style={{padding:"20px"}}>
                            <Descriptions title={`${this.props.currentDataSource.dataSourceType}链接信息`} column={1} bordered>
                            <Descriptions.Item label="数据源名称:">{this.props.currentDataSource.dataSourceName}</Descriptions.Item>
                            <Descriptions.Item label="服务器IP:">{this.props.currentDataSource.host}</Descriptions.Item>
                            <Descriptions.Item label="服务器端口:">{this.props.currentDataSource.port}</Descriptions.Item>
    
                            <Descriptions.Item label={(<span>{this.props.currentDataSource.serviceType === '1' ? '服务名': 'SID'}</span>)}>{this.props.currentDataSource.serviceName}</Descriptions.Item>
    
                            <Descriptions.Item label="用户名:">{this.props.currentDataSource.userName}</Descriptions.Item>
                            <Descriptions.Item label="密码:">{this.props.currentDataSource.password}</Descriptions.Item>
                            <Descriptions.Item label="模式:">{this.props.currentDataSource.schemaName}</Descriptions.Item>
                            <Descriptions.Item label="URL:">{this.props.currentDataSource.url}</Descriptions.Item>
                        </Descriptions>
                    </div>
                )
            } else {
                return (
                    <div style={{padding:"20px"}}>
                            <Descriptions title={`${this.props.currentDataSource.dataSourceType}链接信息`} column={1} bordered>
                            <Descriptions.Item label="数据源名称:">{this.props.currentDataSource.dataSourceName}</Descriptions.Item>
                            <Descriptions.Item label="服务器IP:">{this.props.currentDataSource.host}</Descriptions.Item>
                            <Descriptions.Item label="服务器端口:">{this.props.currentDataSource.port}</Descriptions.Item>
                            <Descriptions.Item label="数据库名:">{this.props.currentDataSource.databaseName}</Descriptions.Item>
                            <Descriptions.Item label="用户名:">{this.props.currentDataSource.userName}</Descriptions.Item>
                            <Descriptions.Item label="密码:">{this.props.currentDataSource.password}</Descriptions.Item>
                            <Descriptions.Item label="模式:">{this.props.currentDataSource.schemaName}</Descriptions.Item>
                            <Descriptions.Item label="URL:">{this.props.currentDataSource.url}</Descriptions.Item>
                        </Descriptions>
                    </div>
                )
            }
        } else {
            return  null
        }
    }
}

export default RightPart