import React, { Component } from 'react'
import {Card} from 'antd'
import LeftPart from './LeftPart'
import RightPart from './RightPart'
import DataTabs from '../common/dataTab';
//数据源管理
export default class dataSourceManagement extends Component {

    state= {
        currentDataSource: null
    }

    changeCurrentDataSource = (obj) => {
        this.setState({
            currentDataSource: {...obj}
        })
    }
    clearDataSource=()=>{
        this.setState({
            currentDataSource:null
        })
    }
    render() {
        return (
            <React.Fragment>
                <DataTabs activeTab="dataSourceManagement"/>
                <div style={{padding: "20px"}}>
                    <Card title={false}  bodyStyle={{padding: "0px"}}>
                        <div style={{float: "left", width: "50%", minHeight: "480px", borderRight: "1px solid #e8e8e8"}}>
                            <LeftPart  currentDataSource = {this.state.currentDataSource} clearDataSource={this.clearDataSource} changeCurrentDataSource = {this.changeCurrentDataSource} />
                        </div>
                        <div style={{float: "left", width: "50%", minHeight: "480px", borderLeft: "1px solid #e8e8e8"}}>
                            <RightPart currentDataSource = {this.state.currentDataSource} />
                        </div>
                    </Card>
                </div>
            </React.Fragment>
            
        )
    }
}
