import React from 'react'
import {Modal, Card} from 'antd'
import MySQL from './MySQL.png'
import Oracle from './Oracle.png'
import SQLServer from './SQLServer.png'
import AddModal from './AddModal'

const { Meta } = Card;

class AddDataSource extends React.Component{

    state = {
        cardList : [
            {
                title: 'mysql',
                // description:'支持MySQL8.0.13版本',
                pic: MySQL
            },
            {
                title: 'SQL Server',
                // description:'支持SQL Server8.0.13版本',
                pic: SQLServer
            },
            {
                title: 'oracle',
                // description:'Oracle.0.13版本',
                pic: Oracle
            }
        ],
        bool: false,
        clickItem: null
    }

    clickHandle = (item) => {
        this.setState({
            bool: true,
            clickItem: item
        })
    }

    hideModal = () => {
        this.setState({
            bool: false
        })
    }

    render () {
        return (
            <Modal
            title="添加数据源"
            visible={true}
            onCancel={this.props.hideAddModal}
            footer={null}
            width="900px"
            >
                <div style={{width: "100%", overflow:"hidden"}}>
                    {
                        this.state.cardList.map(
                            (item,index) => {
                                return (
                                    <Card
                                        key={index}
                                        onClick = {this.clickHandle.bind(this,item)}
                                        hoverable
                                        bordered={false}
                                        style={{ width: 240, float: "left", margin: "0px 20px", background:"rgba(65,100,228,0.04)" }}
                                        cover={<img alt="" src={item.pic} style={{width: "100px", margin:"20px auto 10px auto"}} />}
                                    >
                                        <Meta style={{textAlign:'center'}} title={item.title} description={item.description} />
                                    </Card>
                                )
                            }
                        )
                    }

                    {
                        this.state.bool
                        ?
                        <AddModal addSuccess={this.props.addSuccess} hideModal={this.hideModal} clickItem = {this.state.clickItem} hideAddModal = {this.props.hideAddModal}  />
                        :
                        null
                    }
                </div>
            </Modal>
        )
    }
}

export default AddDataSource